@tailwind base;
@tailwind components;
@tailwind utilities;


.headerSection {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black;
}

.appLogo > img {
    width: 126px;
    height: 100px
}

.navList {
    font-size: 10px;
}

.navList > ul {
    display: flex;
    font-size: 25px;
    padding-left: 30px;
    margin-right: 10px;
    list-style: none;
   
}

.navList > ul > li {
    padding-left: 30px;
    margin-right: 10px;
}
.searchBar{
    margin: 10px 0;
}


.resCard {
    background-color: #f0f0f0;
    width: fit-content;
    margin: 10px;
    padding: 5px;
    border: 1px solid black
}

.resCard > div> img {
    width: 207px;
    height: 201px;
}

.resSection {
    display: flex;
    flex-wrap: wrap;
}

.functionality {
    display: flex;
    
}

.TopRated {
    margin: 10px 30px;
    
}

.TopRatedBTN {
    background-color: aqua;
    border-radius: 5px;
}

.shimmerUi {
    background-color: #f0f0f0;
    width: fit-content;
    margin: 10px;
    padding: 5px;
    border: 1px solid black;
    height: 400px;
    width: 200px;
}

.shimmer {
    display: flex;
    flex-wrap: wrap;
}
Link{
    margin-right: 5px;
}

.logoutBTN {
    margin-left: 10px;
}

.github {
    display: flex;
    align-items: center;
   column-gap: 80px;
    margin-top: 10px;
}

.github > img {
    width: 170px;
    height: 139px;
}